import { h } from 'vue'
import { layer, LayLayer } from '@layui/layer-vue'
import type { Component, VNode } from 'vue'
import { useNamespace } from '@as-ui/utils/bem'

type ShowDialogProps = typeof LayLayer

export const createContent = (
  component: Component,
  props: Record<string, object>
) => {
  return h(component as any, props)
}
export const showDialog = (
  content: VNode | string,
  options: ShowDialogProps,
  callback?: () => void
) => {
  const ns = useNamespace('dialog')
  const skinClass = ns.e('skin')
  const defaultOptions = {
    title: '对话框',
    skin: skinClass,
    area: ['400px', '300px'],
    maxmin: true,
    btn: [
      {
        text: '确认',
        callback: (id: string) => {
          layer.close(id)
        }
      },
      {
        text: '取消',
        callback: (id: string) => {
          layer.close(id)
        }
      }
    ]
  }
  const __options = Object.assign(defaultOptions, options, { content })
  return layer.open(__options, callback)
}
